.form-one {

    &__line {
        position: relative;
        margin: 0 0 23px;

        &_short {
            margin: 0 0 17px;
        }
    }
    &__input {
        border-radius: 10px;
        width: 100%;
        height: 60px;
        background-color: $color-gray1;
        border: 1px solid $color-gray1;
        font-weight: 500;
        font-size: 15px;
        line-height: 100%;
        color: $color-gray2;
        padding: 0 18px;
        transition: .5s;

        &::placeholder {
            color: $color-gray2;
        }
        &:hover,
        &:focus {
            border-color: $color-gray2;
        }
        &:user-invalid {
            border-color: $color-danger;
        }
        &[type="password"] {
            padding-right: 56px;
        }
    }
    &__parts {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__lost-password {
        text-transform: uppercase;
        font-weight: 700;
        font-size: 12px;
        display: block;
    }
    &__bottom {
        display: flex;
        justify-content: center;
    }
}

.back-link {
    font-weight: 700;
    font-size: 13px;
    text-transform: uppercase;
    color: $color-black;
    fill: $color-black;
    text-decoration: none;
    display: flex;
    align-items: center;
    gap: 7px;

    &:hover {
        fill: $color-main;
    }
    &__icon {
        width: 7px;
        height: 11px;
    }
}